body {
    -ms-overflow-style: none; /* IE and Edge */
    scrollbar-width: none; /* Firefox */
}

body::-webkit-scrollbar {
    display: none;
}

#panel {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
}

@media screen and (orientation: landscape) {
    #panel .camera {
        height: 50%;
        width: 50%;
    }

}

@media screen and (orientation: portrait) {
    #panel .camera {
        height: 50%;
        width: 100%;
    }

    #panel .camera.lost-connection::before {
        content: 'Connection lost';
    }

    #panel .camera.lost-connection {
        height: inherit;
        text-align: right;
    }

    #panel .camera.lost-connection .name {
        display: inline-block;
        position: relative;
    }
}


#panel .camera.lost-connection .name {
    background: orange;
}

#panel .camera.lost-connection img {
    display: none;
}

#panel .camera {
    object-fit: contain;
    position: relative;
    background: lightgrey;
    display: inline-block;
    user-select: none;
}

#panel .camera .header div {
    display: inline-block;
}

#panel .camera .name {
    background: red;
    color: white;
    text-shadow: 2px 2px 2px black;
    margin: 10px;
    padding: 5px;
    border-radius: 3px;
}

#panel .camera .events .has-events {
    visibility: visible;
}

#panel .camera .events .has-events:hover {
    background: rgba(100, 100, 100, 0.8);
    cursor: pointer;
}

#panel .camera .events .has-events:first-of-type {
    border-top-left-radius: calc(var(--size) / 3);
    border-bottom-left-radius: calc(var(--size) / 3);
}

#panel .camera .events .has-events:last-of-type {
    border-top-right-radius: calc(var(--size) / 3);
    border-bottom-right-radius: calc(var(--size) / 3);
}

#panel .camera .events .has-events + .has-events {
    border-left: none;
}

#panel .camera .events * {
    font-size: 0.8em;
    --size: 1.5em;
    border: 1px solid #535353;

    min-width: var(--size);
    height: var(--size);
    text-align: center;
    line-height: var(--size);

    visibility: collapse;

    background: whitesmoke;
    opacity: 0.8;

    padding-left: 0.3em;
    padding-right: 0.3em;
}

#panel .camera .header {
    right: 0;
    position: absolute;
}

#panel .camera img {
    height: 100%;
    width: 100%;
    object-fit: fill;
}


#version {
    position: fixed;
    padding-left: 7px;
    padding-right: 7px;
    background: white;
    opacity: 0.8;
    border-radius: 5px;
    border: 1px solid lightgray;
    color: grey;
    display: inline-block;
    z-index: 100;
}

#version::before {
    content: 'version.  ';
    font-size: 0.7em;
    line-height: 1em;
}